<template>
  <v-container grid-list-md fluid>
 
    <!-- success alert -->
    <div
    class="mt-4"
    v-if="success">
      <app-success-alert 
      @dismissed="onDismissed"
      :text="success"
      class="white--text"></app-success-alert>
    </div>

    <!-- error alert -->
    <div
    class="mt-4"
    v-if="error">
      <app-error-alert 
      @dismissed="onDismissed"
      :text="error"
      class="black--text"></app-error-alert>
    </div>

    <!-- item list -->
    <v-layout 
    row wrap 
    v-if="loading">
      <v-flex xs12 class="text-xs-center mt-5">
        Getting your drinks... 
        <v-progress-circular 
        indeterminate 
        color="green"
        :width="3"
        :size="20"
        ></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout 
    row wrap
    v-if="!itemsLoaded">
      <h3>No drinks to display yet.</h3>
    </v-layout>
    <v-layout 
    row wrap 
    v-if="itemsLoaded"
    class="mt-3">
      <v-flex
        v-for="item in items"
        :key="item.id"
        xs12 sm4
      >
        <v-hover>
          <v-card
          slot-scope="{ hover }"
          :class="`elevation-${hover ? 12 : 2}`"
          height="350px">
            {{item.id}}
            <v-img
              :src="item.imageURL"
              height="180px"
              contain
              style="cursor: pointer"
              @click="openDialog(item)"
            >
            </v-img>

            <v-card-title primary-title
            v-if="item.discount === '0'">
              <div>
                <div class="headline">{{item.itemName}}</div>
                <div>SGD {{item.priceSGD}} each</div>
              </div>
            </v-card-title>

            <v-card-title primary-title
            v-if="item.discount !== '0'">
              <div>
                <div class="headline">{{item.itemName}}</div>
                <s>SGD {{item.priceSGD}} each</s>
                <div> SGD {{item.discountedPrice}} each <span
                class="success--text"> {{item.discount}}% discount!</span></div>
              </div>
            </v-card-title>

            <v-card-actions>
              <v-chip color="warning" text-color="white">
                {{item.itemType}}
              </v-chip>
              <v-spacer></v-spacer>
              
              <!-- view item button --> 
              <v-tooltip bottom>
                <v-btn 
                slot="activator"
                flat
                icon
                color="accent"
                @click="openDialog(item)">
                  <v-icon>all_out</v-icon>
                </v-btn>
                <span>View Item</span>
              </v-tooltip>
              
              <!-- likeItem buttons -->
              <v-tooltip
              bottom>
                <v-btn 
                slot="activator"
                icon 
                v-if="!likedItem(item.itemId)"
                @click="like(item.itemId)">
                  <v-icon color="error">favorite_border</v-icon>
                </v-btn>
                <span>Add to Wishlist</span>
              </v-tooltip>
              <v-tooltip
              bottom>
                <v-btn 
                slot="activator"
                icon 
                v-if="likedItem(item.itemId)"
                @click="unlike(item.itemId)">
                  <v-icon color="error">favorite</v-icon>
                </v-btn>
                <span>Remove from Wishlist</span>
              </v-tooltip>

              <!-- shopping cart buttons -->
              <v-tooltip
              bottom>
                <v-menu
                :close-on-content-click="false"
                v-if="!addedToCart(item.itemId)"
                slot="activator"
                >
                  <v-btn
                  slot="activator"
                  @click="setItemQty(1)"
                  icon>
                    <v-icon color="success">add_shopping_cart</v-icon>
                  </v-btn>
                  <v-card>
                    <v-card-text class="pa-4">
                      <v-text-field
                        color="success"
                        v-model="qty"
                        outline
                        single-line
                        type="number"
                        :rules="rules.qty"
                        suffix="unit(s)"
                      >
                        <v-tooltip
                          slot="append-outer"
                          style="top: -12px"
                          bottom
                        >
                          <v-btn
                          :disabled="!qtyValid"
                          slot="activator"
                          flat
                          icon
                          @click="addToCart(item.itemId)">
                            <v-icon
                            color="success">
                              check
                            </v-icon>
                          </v-btn>
                          Submit Order
                        </v-tooltip>
                      </v-text-field>
                    </v-card-text>
                  </v-card>
                </v-menu>
                <span>Add to Cart</span>
              </v-tooltip>
              <v-tooltip
              bottom>
                <v-menu
                :close-on-content-click="false"
                v-if="addedToCart(item.itemId)"
                slot="activator"
                >
                  <v-btn
                  slot="activator"
                  @click="setItemQty(item.qty)"
                  icon>
                    <v-icon color="success">shopping_cart</v-icon>
                  </v-btn>
                  <v-card>
                    <v-card-text class="pa-4">
                      <v-text-field
                        color="success"
                        v-model="qty"
                        outline
                        single-line
                        type="number"
                        :rules="rules.qty"
                        suffix="unit(s)"  
                      >
                        <v-tooltip
                          slot="append-outer"
                          style="top: -12px"
                          bottom
                        >
                          <v-btn
                          :disabled="!qtyValid"
                          slot="activator"
                          flat
                          icon
                          @click="addToCart(item.itemId)">
                            <v-icon
                            color="success">
                              check
                            </v-icon>
                          </v-btn>
                          Submit Order
                        </v-tooltip>
                        <v-tooltip
                          slot="append-outer"
                          style="top: -12px"
                          bottom
                        >
                          <v-btn
                          slot="activator"
                          flat
                          icon
                          @click="removeFromCart(item.itemId)">
                            <v-icon
                            color="error">
                              clear
                            </v-icon>
                          </v-btn>
                          Remove All
                        </v-tooltip>
                      </v-text-field>
                    </v-card-text>
                  </v-card>
                </v-menu>
                <span>Adjust Item Qty</span>
              </v-tooltip>
              <p
              v-if="addedToCart(item.itemId)"
              >
                x {{item.qty}}
              </p>

            </v-card-actions>
          </v-card>
        </v-hover>
      </v-flex>
    </v-layout>

    <!-- item dialog -->
    <v-dialog
    transition="dialog-bottom-transition"
    height="700px"
    v-model="itemDialog">
      <v-card>
        <v-btn 
        icon @click="closeDialog">
          <v-icon>close</v-icon>
        </v-btn>
        <v-layout 
          row
          v-if="error"
          class="mb-2">
            <v-flex xs12>
              <app-error-alert 
              @dismissed="onDismissed"
              :text="error"
              class="black--text"></app-error-alert>
            </v-flex>
          </v-layout>
          <v-layout 
          row
          v-if="success"
          class="mb-2">
            <v-flex xs12>
              <app-success-alert 
              @dismissed="onDismissed"
              :text="success"></app-success-alert>
            </v-flex>
          </v-layout>
        <v-layout row wrap>
          <v-flex xs12 sm8>
            <v-img
            :src="imageURL"
            height="400px"
            contain
          >
            </v-img>
          </v-flex>
          <v-flex xs12 sm4>
            <v-card-title
            v-if="!discount">
              <div class="headline">
                {{name}} @ SGD {{priceSGD}} each
              </div>
            </v-card-title>
            <v-card-title
            v-if="discount">
              <div class="headline">
                {{name}} @ 
                <s>SGD {{priceSGD}} each</s>
                SGD {{discountedPrice}} each
              </div>
            </v-card-title>
            <v-divider></v-divider>
            <v-layout row wrap>
              <v-card-text>
                <v-layout v-if="loading">
                  <p>Processing...
                    <v-progress-circular
                    indeterminate 
                    color="primary"
                    :width="3"
                    :size="15"
                    ></v-progress-circular>
                  </p>
                </v-layout>
                <v-layout row wrap
                v-if="!loading">
                  <v-list two-line subheader>
                    <v-subheader class="title">{{description}}</v-subheader>

                    <v-list-tile>
                      <v-list-tile-content>
                        <v-list-tile-title>Expected delivery time: {{deliveryTime}} minutes</v-list-tile-title>
                        <v-list-tile-sub-title
                        v-if="discount"
                        class="green--text">Now at {{itemDiscount}}%  discount!</v-list-tile-sub-title>
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-list>
                </v-layout>
              </v-card-text>
            </v-layout>
          </v-flex>
        </v-layout>
        <v-layout 
        align-end
        justify-center
        fill-height
        row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>
              <!-- likeItem buttons -->
              <div>
                This offer expires in {{listPeriod}} days. Get it now!
              </div>
              <v-tooltip
              bottom>
                <v-btn 
                slot="activator"
                icon 
                v-if="!likedItem(itemId)"
                @click="like(itemId)">
                  <v-icon color="error">favorite_border</v-icon>
                </v-btn>
                <span>Add to Wishlist</span>
              </v-tooltip>
              <v-tooltip
              bottom>
                <v-btn 
                slot="activator"
                icon 
                v-if="likedItem(itemId)"
                @click="unlike(itemId)">
                  <v-icon color="error">favorite</v-icon>
                </v-btn>
                <span>Remove from Wishlist</span>
              </v-tooltip>

              <!-- shopping cart buttons -->
              <v-tooltip
              bottom>
                <v-menu
                :close-on-content-click="false"
                v-if="!addedToCart(itemId)"
                slot="activator"
                >
                  <v-btn
                  slot="activator"
                  @click="setItemQty(1)"
                  icon>
                    <v-icon color="success">add_shopping_cart</v-icon>
                  </v-btn>
                  <v-card>
                    <v-card-text class="pa-4">
                      <v-text-field
                        color="success"
                        v-model="qty"
                        outline
                        single-line
                        type="number"
                        :rules="rules.qty"
                        suffix="unit(s)"
                      >
                        <v-tooltip
                          slot="append-outer"
                          style="top: -12px"
                          bottom
                        >
                          <v-btn
                          :disabled="!qtyValid"      
                          slot="activator"
                          flat
                          icon
                          @click="addToCart(itemId)">
                            <v-icon
                            color="success">
                              check
                            </v-icon>
                          </v-btn>
                          Submit Order
                        </v-tooltip>
                      </v-text-field>
                    </v-card-text>
                  </v-card>
                </v-menu>
                <span>Add to Cart</span>
              </v-tooltip>
              <v-tooltip
              bottom>
                <v-menu
                :close-on-content-click="false"
                v-if="addedToCart(itemId)"
                slot="activator"
                >
                  <v-btn
                  slot="activator"
                  @click="setItemQty(item.qty)"
                  icon>
                    <v-icon color="success">shopping_cart</v-icon>
                  </v-btn>
                  <v-card>
                    <v-card-text class="pa-4">
                      <v-text-field
                        color="success"
                        v-model="qty"
                        outline
                        single-line
                        type="number"
                        :rules="rules.qty"
                        suffix="unit(s)"
                      >
                        <v-tooltip
                          slot="append-outer"
                          style="top: -12px"
                          bottom
                        >
                          <v-btn
                          :disabled="!qtyValid"
                          slot="activator"
                          flat
                          icon
                          @click="addToCart(itemId)">
                            <v-icon
                            color="success">
                              check
                            </v-icon>
                          </v-btn>
                          Submit Order
                        </v-tooltip>
                        <v-tooltip
                          slot="append-outer"
                          style="top: -12px"
                          bottom
                        >
                          <v-btn
                          slot="activator"
                          flat
                          icon
                          @click="removeFromCart(itemId)">
                            <v-icon
                            color="error">
                              clear
                            </v-icon>
                          </v-btn>
                          Remove All
                        </v-tooltip>
                      </v-text-field>
                    </v-card-text>
                  </v-card>
                </v-menu>
                <span>Adjust Item Qty</span>
              </v-tooltip>
              <p
              v-if="addedToCart(itemId)"
              >
                x {{qty}}
              </p>
              <v-btn 
              flat 
              class="blue--text darken-1"
              @click="closeDialog">Close</v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      buyerId: null,
      itemDialog: false,
      itemId: null,
      imageURL: '',
      name: '',
      description: '',
      priceSGD: null,
      itemDiscount: null,
      deliveryTime: null,
      listPeriod: null,
      qty: 1,
      rules: {
        qty: [val => val > 0 || 'Enter 1 or more in quantity please.']
      }
    }
  },
  mounted () {
    this.init()
  },
  computed: {
    ...mapGetters({
      items: 'getUpdatedItems',
      likedItems: 'getLikedItems',
      cartItems: 'getCartItems'
    }),
    itemsLoaded () {
      return this.items != null || undefined
    },
    user () {
      return this.$store.getters.getUser
    },
    vendor () {
      return this.$store.getters.getVendor
    },
    discount () {
      return this.itemDiscount > 0
    },
    discountedPrice () {
      return this.priceSGD * (1 - this.itemDiscount / 100)
    },
    error () {
      return this.$store.getters.getError
    },
    success () {
      return this.$store.getters.getSuccess
    },
    loading () {
      return this.$store.getters.getLoading
    }
  },
  methods: {
    // initialization functions to be called on mounted lifecycle hook
    init () {
      // load all available items
      this.$store.dispatch('itemsApi')
      if (this.user) {
        this.buyerId = this.user.id
        // load all liked items
        this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
      } else if (this.vendor) {
        this.buyerId = this.vendor.id
        // load all liked items
        this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
      }
    },
    // dismiss alerts
    onDismissed () {
      this.$store.dispatch('clearError')
      this.$store.dispatch('clearSuccess')
    },
    // item dialog methods
    openDialog (item) {
      // open itemDialog
      this.itemDialog = true
      // set dialog params
      this.itemId = item.itemId
      this.imageURL = item.imageURL
      this.name = item.itemName
      this.description = item.description
      this.priceSGD = item.priceSGD
      this.itemDiscount = item.discount
      this.listPeriod = item.listPeriod
      this.deliveryTime = item.deliveryTime
      this.qty = item.qty
      this.$store.dispatch('setError', {message: 'Only consumers 18 years old and above may purchase and consume alcohol! Please enjoy responsibly.'})
    },
    closeDialog () {
      // close itemDialog
      this.itemDialog = false
      this.onDismissed()
      // reset dialog params
      this.itemId = ''
      this.imageURL = ''
      this.name = ''
      this.description = ''
      this.priceSGD = null
      this.itemDiscount = null
      this.listPeriod = null
      this.deliveryTime = null
      this.qty = 1
    },
    like (itemId) {
      if (this.buyerId) {
        let payload = {
          userId: this.buyerId,
          itemId: itemId
        }
        this.$store.dispatch('likeItem', payload)
        this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
      } else {
        this.$store.dispatch('setSuccess', {message: 'Please login and/or register to add item to your wishlist!'})
      }
    },
    unlike (itemId) {
      if (this.buyerId) {
        let payload = {
          userId: this.buyerId,
          itemId: itemId
        }
        this.$store.dispatch('unlikeItem', payload)
        this.$store.dispatch('likedItemsApi', {userId: this.buyerId})
      }
    },
    likedItem (itemId) {
      if (this.buyerId) {
        if (this.likedItems === null || undefined) {
          return false
        } else {
          let liked = false
          for (let i = 0; i < this.likedItems.length; i++) {
            if (this.likedItems[i].itemId === itemId) {
              liked = true
            }
          }
          return liked
        }
      } else {
        return false
      }
    },
    addToCart (itemId) {
      if (this.buyerId) {
        let payload = {
          userId: this.buyerId,
          itemId: itemId,
          qty: this.qty
        }
        this.$store.dispatch('addItemToCart', payload)
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
        this.getItemQty(payload)
      } else {
        this.$store.dispatch('setSuccess', {message: 'Please login and/or register to add item to your shopping cart!'})
      }
    },
    removeFromCart (itemId) {
      if (this.buyerId) {
        let payload = {
          userId: this.buyerId,
          itemId: itemId
        }
        this.$store.dispatch('removeItemFromCart', payload)
        this.$store.dispatch('cartItemsApi', {userId: this.buyerId})
      }
    },
    addedToCart (itemId) {
      if (this.buyerId) {
        if (this.cartItems === null || undefined) {
          return false
        } else {
          let added = false
          for (let i = 0; i < this.cartItems.length; i++) {
            this.getItemQty(this.cartItems[i])
            if (this.cartItems[i].itemId === itemId) {
              added = true
            }
          }
          return added
        }
      } else {
        return false
      }
    },
    qtyValid () {
      if (this.qty > 0) {
        return true
      } else {
        return false
      }
    },
    getItemQty (payload) {
      for (let i = 0; i < this.items.length; i++) {
        if (this.items[i].itemId === payload.itemId) {
          this.items[i].qty = payload.qty
        }
      }
    },
    setItemQty (itemQty) {
      this.qty = itemQty
    }
  }
}
</script>




